import React from "react";
const gameImages = require.context('./../../../assets/img', true);

const GameItem = ({ gameName }) => {
    const imagePath = gameImages(`./${gameName}.png`);
    console.log(imagePath)

    return (
        <div className="game-item col-span-6 sm:col-span-6 md:col-span-4 lg:col-span-2 transition duration-300 ease-in-out transform hover:-translate-y-1 cursor-pointer">
            <img src={imagePath} alt={gameName} className="game-image rounded-[8px] hover:brightness-75" />
        </div>
    );
};

export default GameItem;
